import React from 'react';
import { compose, lifecycle, withStateHandlers } from "recompose";
import styled from 'styled-components'

import ContentClient from 'lib/content-client'

import ArtworkList from './components/ArtworkList';
import ArtworkDetail from './components/ArtworkDetail';

const ArtworkWrapper = styled.div`
  max-width: 780px;
  margin: 0 auto;
  display: inline-flex;
  flex-direction: column;
  margin: 0 20px;
`

const Artwork = ({ artworkContent }) => {
  if (!artworkContent) {
    return <div>...</div>
  }

  const { 
    primaryCollection,
    restCollections,
    title,
    description,
    client,
    service,
    year,
  } = artworkContent.fields;

  return (
    <ArtworkWrapper>
      <ArtworkList artworkImgList={primaryCollection} />
      <ArtworkDetail
        title={title}
        description={description}
        client={client}
        service={service}
        year={year}
      />
      <ArtworkList artworkImgList={restCollections} />
    </ArtworkWrapper>
  )
}

const enhance = compose(
  withStateHandlers(
    { artworkContent: null },
    {
      setArtworkContent: () => (payload) => ({
        artworkContent: payload,
      })
    }
  ),
  lifecycle({
    componentDidMount() {
      const { match, setArtworkContent } = this.props
      const { artwork } = match.params
      ContentClient.getEntry(artwork).then(entry => {
        const { collections } = entry.fields;
        const primaryCollection = [collections[0]] || null;
        const restCollections = collections.length && collections.slice(1);

        const updatedEntry = { 
          ...entry,
          fields: {
            ...entry.fields,
            primaryCollection,
            restCollections,
          }
        };
        setArtworkContent(updatedEntry);
      })
    }
    
  })
)(Artwork)

export default enhance;