import React from 'react';
import { Link } from "react-router-dom";
import styled from 'styled-components';

import FadeIn from "components-common/FadeIn";

import { imageScalerByWidth } from "lib/layout-utils";

const Item = styled.div`
  margin-top: 10px;
  position: relative;
`
const ItemImg = styled.img`
  width: 100%;

  ${Item}:hover & {
    opacity: .3;
  }
`
const ItemOverlay = styled.div`
  position: absolute;
  top: 40%;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  color: #3e3e3e;
  font-family: 'Oswald';
  font-size: 24px;

  ${Item}:hover & {
    opacity: 1;
  }
`

const GalleryItem = ({
  artworkId,
  title,
  slug,
  cover,
}) => {
  const { file: coverImgFile } = cover.fields;
  const { 
    url: coverImgUrl,
    details: coverImgDetails,
  } = coverImgFile;
  const { 
    image: { 
      height: coverImgHeight, 
      width: coverImgWidth,
    } 
  } = coverImgDetails;
  const coverImgScaledHeight = imageScalerByWidth(coverImgWidth, coverImgHeight, 240);
  console.log('scaled height :: ', coverImgScaledHeight);
  return (
    <Link to={`/artworks/${artworkId}`}>
      <Item>
        <FadeIn height={coverImgScaledHeight}>
          {onLoad => 
            <ItemImg src={coverImgUrl} alt={slug} onLoad={onLoad}/>
          }
        </FadeIn>
        <ItemOverlay>
          <div>
            {title}
          </div>
        </ItemOverlay>
      </Item>
    </Link>
  )
}

export default GalleryItem;
