import React from 'react';
import styled from 'styled-components';

import moment from 'moment'

const Detail = styled.div`
  text-align: left;
`
const Title = styled.h1`
  font-size: 25px;
  font-family: 'Oswald';
  font-weight: 100;
  margin-top: 15px;
`
const Description = styled.p`
  padding-top: 20px;
`
const DetailInfoItem = styled.div`
  width: 33.33%;
`
const DetailInfoRow = styled.div`
  display: flex;
  padding: 40px 0 40px 0;
  ${DetailInfoItem}:nth-child(2) {
    text-align: center;
    & > div {
      display: inline-block;
      text-align: left;
    }
  }
  ${DetailInfoItem}:nth-child(3) {
    text-align: right;
  }
`


const ArtworkDetail = ({ 
  title,
  description,
  client,
  service,
  year,
}) => {
  const yearNormalized = moment(year).format('YYYY');
  return (
    <Detail>
      <Title>{title}</Title>
      <Description>{description}</Description>
      <DetailInfoRow>
        <DetailInfoItem>
          <h3>Client</h3>
          <p>{client}</p>
        </DetailInfoItem>
        <DetailInfoItem>
          <div>
            <h3>Service</h3>
            <p>{service}</p>
          </div>
        </DetailInfoItem>
        <DetailInfoItem>
          <h3>Year</h3>
          <p>{yearNormalized}</p>
        </DetailInfoItem>
      </DetailInfoRow>
    </Detail>
  )

}

export default ArtworkDetail;