import React from 'react';
import styled from 'styled-components';

const ArtworkItem = styled.div`
  margin-top: 15px;
`
const ArtworkImg = styled.img`
  width: 100%;
`

const ArtworkList = ({ artworkImgList }) => {
  const artworkList =  artworkImgList && artworkImgList.map(artworkImg => {
    const { 
      fields: { file: imgFile } = {}, 
      sys: { id: imgId } = {},
    } = artworkImg;
    const { url: imgUrl } = imgFile

    return (
      <ArtworkItem key={imgId}>
        <ArtworkImg src={imgUrl} alt='artwork' />
      </ArtworkItem>
    );
  });

  return artworkList;
}

export default ArtworkList;