import React from 'react';
import styled from 'styled-components'

import { MOBILE_DEVICE_WIDTH, GALLERY_COLUMN_WIDTH } from "constants/view";

import GalleryItem from './GalleryItem';

const Column = styled.div`
  width: ${GALLERY_COLUMN_WIDTH}px;
  margin: 0 8px;

  @media (max-width: ${MOBILE_DEVICE_WIDTH}px) {
    width: auto;
    margin: 0 20px;
  }
`

const GalleryCol = ({ artworks }) => (
  <Column>
    {artworks.map((artwork, i) => {
      const { fields, sys } = artwork
      return <GalleryItem key={sys.id} artworkId={sys.id} {...fields} />
    })}
  </Column>
)

export default GalleryCol;